html, body {
  scroll-behavior: smooth;
}

.mainbox {
  width: 960px;
  margin: 0 auto;
}

nav {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  position: relative;
  width: 100%;
  height: 50px;
  background-color: #4E4E4E;
}

.navbar {
  font-family: 'Roboto', sans-serif;
  font-size: 18px;
  line-height: 21px;
  color: #F1F1F1;
}

.nav1 {
  color: #ffffff;
  position: absolute;
  left: 94px;
  font-weight: bold;
}

.nav2 {
  position: absolute;
  right: 90px;
  text-decoration: none;
}

.nav3 {
  position: absolute;
  right: 197px;
}

.nav4 {
  position: absolute;
  right: 292px;
}

.nav4:disabled {
  position: absolute;
  right: 292px;
}

.nav4:hover {
  cursor:not-allowed;
}

