  left: 94px;
  top: 300px;
}

.landingbtn {
  font-family: 'Roboto', sans-serif;
  font-weight: bold;
  font-size: 18px;
  line-height: 21px;
  color: #ffffff;
  box-sizing: border-box;
  border-style: solid;
  border-width: 3px;
  border-color: #ffffff;
  padding: 14px 57px;
  background-color: transparent;
  position: absolute;
  top: 362px;
  left: 94px;
  text-decoration: none;
}

.landingbtn:hover {
  transition: 1s;
  transition-timing-function: linear;
  background-color: #ffffff;
  color: #000000;
}

