}

.boxdot {
  width: 22px;
  height: 22px;
  border-radius: 50%;
  background-color: #E07A64;
  position: absolute;
  top: -11px;
  left: -11px;
}

.childbox {
  height: 194px;
  width: 198px;
  background-color: #F1F1F1;
  display: flex;
