  flex-direction: column;
  font-family: 'Roboto', sans-serif;
  font-size: 24px;
  line-height: 28px;
  position: relative;
}

.childbox:hover {
  transform: scale(1.05);
  transition: 0.5s;
  transition-timing-function: linear;
}

.box1text {
